.meeting-custom-info-content iframe {
  border: none !important;
  border-radius: 8px;
}
.video-fluid {
  position: relative;
  padding-bottom: 56.25%; /* 16:9 format */
  height: 0;
  border-radius: 8px;
  overflow: hidden;
}
.video-fluid iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border: none;
  border-radius: 8px;
}
